import redirect from '../../mixins/redirect.js';
import GoodsLists from '../../components/lists/GoodsLists.vue';
import uniLoadMore from '@dcloudio/uni-ui/lib/uni-load-more/uni-load-more.vue';
export default {
    mixins: [redirect],
    components:{
        GoodsLists,
        uniLoadMore
    },
    data() {
        return {
            keyword:'',
            goodsLists: [],
            isList:false,
            loadding:false,
            loadStatus: 'more',
            pullUpOn : true,
            shop_id:'',
            detail:{},
            currentPage:1,
            lastPage:''
        };
    },
    onLoad:function(option){
      this.shop_id = option.shopid
      this.getDetail()
      this.getShopGoods(true)
    },
    methods: {
        onSearch(){
          this.getShopGoods(true)
        },
        fansClick(type){
          if(type==='cancel'){
            this.$http({
              aurl:'cancelShopFavorite',
              params:{
                shop_id:this.shop_id
              },
              success:res => {
                uni.showToast({
                  title: '取消关注成功',
                  icon: 'none',
                })
                this.detail.is_favorite = 0
              },
              fail:error => {
                console.log(error)
              }
            })
          }else{
            this.$http({
              aurl:'ShopFavorite',
              params:{
                shop_id:this.shop_id
              },
              success:res => {
                uni.showToast({
                  title: '关注成功',
                  icon: 'none',
                })
                this.detail.is_favorite = 1
              },
              fail:error => {
                console.log(error)
              }
            })
          }

        },
        getDetail(){
          this.$http({
            aurl:'shopDetail',
            aPath:[this.shop_id],
            success:res => {
              this.detail = res.data.result
            },
            fail:error => {
              console.log(error)
            }
          })
        },
        getShopGoods(init){
          if(init){
            this.currentPage = 1;
            this.goodsLists = [];
            this.pullUpOn = true;
            this.loadding = true;
            this.loadStatus = 'More';
          }
          this.$http({
            aurl:'getCateList',
            params:{
              shop_id:this.shop_id,
              page:this.currentPage,
              keyword:this.keyword
            },
            success:res => {
              this.lastPage = res.data.result.last_page
              res.data.result.data.forEach(item => {
                this.goodsLists.push(item);
              });
              if (res.data.result.last_page === 1) {
                this.loadding = false;
                this.pullUpOn = false;
                this.loadStatus = 'noMore';
              }
            },
            fail:error => {
              console.log(error)
            }
          })
        },
        cleanKeyword() {
            this.keyValue = '';
        },
    },
  onReachBottom() {
    if (!this.pullUpOn) return;
    this.loadding = true;
    this.loadStatus = 'loading';
    if (this.lastPage === this.currentPage) {
      this.loadding = false;
      this.pullUpOn = false;
      this.loadStatus = 'noMore';
    } else {
      this.currentPage++;
      setTimeout(() => {
        this.getShopGoods();
        this.loadding = false;
        this.loadStatus = 'more';
      }, 500);
    }
  },
    mounted() {
    },
};
